import * as React from 'react';
import TextField from '@mui/material/TextField';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import StaticDatePicker from '@mui/lab/StaticDatePicker';
import { PickersDay } from '@mui/lab';
import moment from 'moment';
export default function StaticDatePickerLandscape() {
  const [value, setValue] = React.useState<Date | null>(new Date());

  return (
    <LocalizationProvider dateAdapter={AdapterDateFns}>
      <StaticDatePicker<Date>
        displayStaticWrapperAs="desktop"
        showDaysOutsideCurrentMonth
        orientation="portrait"
        openTo="day"
        renderDay={(day, selectedDay, pickerDateProps) => {
          const pickedDays = [12, 17, 28];
          return (
            <div className="position-relative">
              {pickedDays.includes(moment(day).date()) && (
                <span
                  style={{
                    padding: '3px',
                    top: '5%',
                    zIndex: 1100,
                  }}
                  className="position-absolute start-50 translate-middle-x bg-primary rounded-circle"
                />
              )}

              <PickersDay
                {...pickerDateProps}
                sx={{
                  '&.Mui-selected': {
                    borderRadius: '6px',
                    backgroundColor: '#F5F1FF !important',
                    color: '#0F0F0F',
                  },
                }}
              />
            </div>
          );
        }}
        value={value}
        onChange={(newValue) => {
          setValue(newValue);
        }}
        renderInput={(params) => <TextField {...params} />}
      />
    </LocalizationProvider>
  );
}
