import moment from 'moment';
import React from 'react';

interface Props {
  date: any;
}

const RenderDateTag = (props: Props) => {
  const date = moment(props.date).date();
  const month = moment(props.date).format('MMMM');
  return (
    <div className="d-flex flex-column rounded bg-light align-items-center py-2">
      <span className="fw-bold text-muted fs-4">{date}</span>
      <span className="text-uppercase text-muted">{month.slice(0, 3)}</span>
    </div>
  );
};

export default RenderDateTag;
