import React from 'react';
import TopProcessBar, { StepProcessProps } from './TopProcessBar';
import SchoolOutlinedIcon from '@mui/icons-material/SchoolOutlined';
import { Container, Form, Button, Image, Badge, Row } from 'react-bootstrap';
import { useFormik } from 'formik';
import TextIconInput from 'components/input/TextIconInput';
import SearchIcon from '@mui/icons-material/Search';
import TuneIcon from '@mui/icons-material/Tune';
import { Card } from '@mui/material';
import { schoolOptionsData } from 'components/helper/countryPhoneCode';
import debounce from 'lodash/debounce';
import CircleOutlinedIcon from '@mui/icons-material/CircleOutlined';
import CircleIcon from '@mui/icons-material/Circle';
import TextInput from 'components/input/textInput';
import RemoveCircleOutlineRoundedIcon from '@mui/icons-material/RemoveCircleOutlineRounded';
import HighlightOffRoundedIcon from '@mui/icons-material/HighlightOffRounded';
import SelectInput from 'components/input/SelectInput';
interface Props {}

const SelecCollegePage = (props: Props) => {
  const stepProcess: StepProcessProps[] = [
    { title: 'Select College', percentOnProcess: 12 },
    { title: 'Select Major', percentOnProcess: 75 },
  ];
  const [schoolOptions, setSchoolOptions] = React.useState<any[]>([]);
  const [showFilter, setShowFilter] = React.useState(false);
  const [stateOptions, setStateOptions] = React.useState<any[]>([]);
  const [publicOptions, setPublicOptions] = React.useState<any[]>([]);
  const [selectedCollege, setSelectedCollege] = React.useState<any>(undefined);
  const formik = useFormik({
    initialValues: {
      major1: '',
      major2: '',
      zipcode: undefined,
      state: undefined,
      type: undefined,
    },
    onSubmit: async (values) => {
      alert(values);
    },
  });

  React.useEffect(() => {
    setSchoolOptions(schoolOptionsData);

    setStateOptions([
      { name: 'All', value: null },
      ...schoolOptionsData.map((item) => {
        return { name: item.location, value: item.location };
      }),
    ]);
    setPublicOptions([
      { name: 'All', value: null },
      { name: 'Public', value: true },
      { name: 'Privated', value: false },
    ]);
  }, []);

  const handleSelectCollege = (school: any) => {
    if (selectedCollege === school) {
      setSelectedCollege(undefined);
    } else {
      setSelectedCollege(school);
    }
  };

  const handleSearchSchool = debounce((e) => {
    let newSchools = schoolOptionsData.filter((school) => school.name.includes(e.target?.value));
    if (formik.values.type && formik.values.type !== 'All') {
      newSchools = newSchools.filter((school) => school.isPublic.toString() === formik.values.type);
    }
    if (formik.values.state && formik.values.state !== 'All') {
      newSchools = newSchools.filter((school) => school.location === formik.values.state);
    }
    setSchoolOptions(newSchools);
  }, 400);

  React.useEffect(() => {
    let newSchools = schoolOptionsData;
    if (formik.values.type && formik.values.type !== 'All') {
      newSchools = newSchools.filter((school) => school.isPublic.toString() === formik.values.type);
    }
    if (formik.values.state && formik.values.state !== 'All') {
      newSchools = newSchools.filter((school) => school.location === formik.values.state);
    }
    setSchoolOptions(newSchools);
  }, [formik.values.type, formik.values.state]);

  return (
    <>
      <TopProcessBar
        stepProcess={stepProcess}
        title="College"
        process={stepProcess[0].percentOnProcess}
        icon={<SchoolOutlinedIcon />}
      />
      <Container>
        <div className="d-flex justify-content-center mt-3">
          <Form style={{ width: '1000px' }} onSubmit={formik.handleSubmit}>
            <div className="text-dart" style={{ fontWeight: 'bolder', fontSize: '1.5rem' }}>
              What colleges are you interested in?
            </div>
            <div className="text-muted sized70 fw-regular mb-2">
              Don't worry, you can change them later if you'd like.
            </div>
            <div className="text-muted">
              Add the colleges you have an eye on and the majors your interested in applying into
            </div>

            <div className="w-100 d-flex">
              <div style={{ width: 'calc(100% - 64px' }}>
                <TextIconInput mdCol={'12'} icon={<SearchIcon />} onChange={handleSearchSchool} />
              </div>
              <Button
                variant="light"
                className=" p-0 m-auto"
                style={{ width: '40px', height: '40px' }}
                onClick={() => setShowFilter(true)}
              >
                <TuneIcon />
              </Button>
            </div>
            {showFilter && (
              <div
                style={{ height: '140px' }}
                className="bg-light w-100 rounded p-3 d-flex flex-wrap justify-content justify-content-between"
              >
                <div>Use these filters to find schools that fit you:</div>
                <div>
                  <span
                    className="pointer text-primary"
                    onClick={() => {
                      formik.resetForm();
                    }}
                  >
                    Clear filter
                  </span>
                  <HighlightOffRoundedIcon
                    className="pointer text-muted mx-2"
                    onClick={() => {
                      setShowFilter(false);
                      formik.resetForm();
                    }}
                  />
                </div>
                <Row className="w-100">
                  <SelectInput
                    mdCol={'4'}
                    label="US State"
                    option={stateOptions}
                    name="state"
                    values={formik.values.state}
                    onChange={formik.handleChange}
                  />
                  <SelectInput
                    mdCol={'4'}
                    label="Type of College"
                    option={publicOptions}
                    name="type"
                    values={formik.values.type}
                    onChange={formik.handleChange}
                  />
                  <TextInput
                    mdCol={'4'}
                    label="Zip Code"
                    name="zipcode"
                    placeholder="Label"
                    values={formik.values.zipcode}
                    onChange={formik.handleChange}
                  />
                </Row>
              </div>
            )}

            <div className="w-100 d-flex flex-wrap ">
              {schoolOptions.map((school, index) => (
                <Card
                  key={index}
                  style={{
                    width: '280px',
                    height: '92px',
                    border: school?.name === selectedCollege?.name ? '1px solid #5f30e2' : '',
                  }}
                  className="rounded d-flex align-items-center my-3 mx-auto mx-md-0 me-md-5 position-relative pointer"
                  onClick={() => handleSelectCollege(school)}
                >
                  <Image width="35px" height="35px" src={school.icon} className="mx-3" />
                  <div>
                    <Badge
                      bg="light text-uppercase"
                      text={school?.name === selectedCollege?.name ? 'primary' : 'dark'}
                    >
                      {school.isPublic ? 'Public' : 'Privated'}
                    </Badge>
                    <div>{school.name}</div>
                    <div className="sized70 text-muted">{school.location}</div>
                  </div>
                  {school?.name === selectedCollege?.name ? (
                    <CircleIcon className="position-absolute top-0 end-0 text-primary mt-1 me-1 sized70" />
                  ) : (
                    <CircleOutlinedIcon className="position-absolute top-0 end-0 rounded-circle sized70 text-muted mt-1 me-1" />
                  )}
                </Card>
              ))}
            </div>

            <div className="w-100 my-3 text-center">{schoolOptions.length} results</div>

            <div className="w-100 border my-5" />

            <div className="text-dart" style={{ fontWeight: 'bolder', fontSize: '1.5rem' }}>
              Add a major
            </div>
            <div className="text-muted">
              Add a major - don't stress, you can change it later, if necessary.
            </div>
            {selectedCollege ? (
              <Card className="rounded my-3 me-5 w-100 p-3 py-3">
                <div className="w-100 d-flex align-items-center justify-content-between">
                  <div className="my-1 d-flex align-items-center">
                    <Image width="35px" height="35px" src={selectedCollege.icon} className="mx-3" />
                    <div className="mx-2 me-3">
                      <div>{selectedCollege.name}</div>
                      <div className="sized70 text-muted">{selectedCollege.location}</div>
                    </div>
                    <Badge bg="light text-uppercase" text="primary">
                      {selectedCollege.isPublic ? 'Public' : 'Privated'}
                    </Badge>
                  </div>
                  <RemoveCircleOutlineRoundedIcon
                    className="text-muted pointer"
                    onClick={() => setSelectedCollege(undefined)}
                  />
                </div>

                <div className="border w-100 my-3"></div>
                <Row>
                  <TextInput
                    mdCol={'6'}
                    label="Major No.1"
                    name="major1"
                    placeholder="Example: Medicine"
                    values={formik.values.major1}
                    onChange={formik.handleChange}
                  />
                  <TextInput
                    mdCol={'6'}
                    label="Major No.2"
                    name="major2"
                    placeholder="Example: Arts"
                    values={formik.values.major2}
                    onChange={formik.handleChange}
                  />
                </Row>
              </Card>
            ) : (
              <div
                className="bg-light rounded d-flex flex-column justify-content-center align-items-center w-100 my-3"
                style={{ height: '120px', border: '2px dashed lightgrey' }}
              >
                <div className="fs-5 text-dart mb-2">No Colleges added, yet</div>
                <div>
                  Select the colleges that you would like to apply to, and continue the process.
                </div>
              </div>
            )}

            <div className="w-100 border my-5" />
            <div className="w-100 text-end mb-5">
              <Button variant="primary" className="px-5" disabled={!selectedCollege}>
                Continue
              </Button>
            </div>
          </Form>
        </div>
      </Container>
    </>
  );
};

export default SelecCollegePage;
